import React from 'react'
import { motion } from "framer-motion";

const Button = ({ setVariant }) => {
  return (
    <button onClick={() => setVariant((prev) => !prev)}>
        <svg width="23" height="23" viewBox="0 0 23 23">
            <motion.path strokeWidth="3" stroke="black" strokeLinecap="round" d="M 2 2.5 L 20 2.5" variants={{secondary: { d: "M 2 2.5 L 20 2.5" }, primary: { d: "M 3 16.5 L 17 2.5" },}}/>
            <motion.path strokeWidth="3" stroke="black" strokeLinecap="round" d="M 2 9.423 L 20 9.423" variants={{secondary: { opacity: 1 },primary: { opacity: 0 },}}/>
            <motion.path strokeWidth="3" stroke="black" strokeLinecap="round" d="M 2 16.346 L 20 16.346" variants={{secondary: { d: "M 2 16.346 L 20 16.346" },primary: { d: "M 3 2.5 L 17 16.346" },}}/>
        </svg>
    </button>
  )
}

export default Button